const CATEGORY_ICONS: Record<Category, string> = {
  [Category.RECICLAGEM]: 'ðŸ”„', [Category.MILHO]: 'ðŸŒ½', [Category.SOJA]: 'ðŸŒ±',
  [Category.BOVINOCULTURA]: 'ðŸ‚', [Category.ECONOMIA]: 'ðŸ’²', [Category.SUINOCULTURA]: 'ðŸ–',
  [Category.AVICULTURA]: 'ðŸ”', [Category.BIODIESEL]: 'â›½', [Category.RACOES_PET]: 'ðŸ•',
  [Category.PISCICULTURA]: 'ðŸŸ'
};

const ensureHttps = (url: string) => {
  if (!url || url === "#") return "#";
  if (url.startsWith('http')) return url;
  if (url.startsWith('//')) return `https:${url}`;
  return `https://${url}`;
};

// --- COMPONENTE: SANITARY SHIELD ---
const SanitaryShield: React.FC<{ risk: SanitaryRisk }> = ({ risk }) => {
  if (!risk || risk.classificationType !== 'SANITARIO') return null; 
  const style = risk.epidemicLevel === 'PANZOOTIA' ? 'bg-red-50 border-red-500 text-red-900' : 
                risk.epidemicLevel === 'EPIDEMIA_REGIONAL' ? 'bg-orange-50 border-orange-500 text-orange-900' : 
                'bg-blue-50 border-blue-500 text-blue-900';
  
  return (
    <div className={`mt-6 mb-8 p-6 rounded-2xl border-l-8 shadow-sm ${style}`}>
      <div className="flex justify-between items-start mb-4">
        <div className="flex items-center gap-3">
          <Skull className="w-6 h-6 opacity-80"/>
          <div>
            <h3 className="font-black uppercase tracking-widest text-sm opacity-60">Alerta SanitÃ¡rio</h3>
            <div className="text-xl font-black">{risk.diseaseName}</div>
          </div>
        </div>
      </div>
    </div>
  );
};

// --- MODAL DE LEITURA ---
const DiscoveryModal: React.FC<{ report: DiscoveryReport; onClose: () => void }> = memo(({ report, onClose }) => {
  if (!report) return null;
  return (
    <div className="fixed inset-0 bg-slate-900/70 backdrop-blur-md z-[100] flex items-center justify-center p-4">
      <div className="bg-white rounded-[40px] w-full max-w-4xl max-h-[90vh] overflow-hidden shadow-2xl flex flex-col">
        <div className="p-8 border-b flex justify-between items-center bg-slate-50">
          <h2 className="text-2xl font-black text-slate-900">{report.title}</h2>
          <button onClick={onClose} className="p-3 hover:bg-slate-200 rounded-2xl transition-all"><X className="w-6 h-6"/></button>
        </div>
        <div className="p-10 overflow-y-auto">
          <div className="prose prose-slate max-w-none" dangerouslySetInnerHTML={{ __html: report.content || '' }} />
        </div>
      </div>
    </div>
  );
});

// --- COMPONENTE: PILAR CARD ---
const PillarCard: React.FC<{ title: string, weight: string, factors?: PredictionFactor[], color: string }> = ({ title, weight, factors, color }) => {
  return (
    <div className="bg-white rounded-2xl border border-slate-100 p-6 shadow-sm flex-1 min-w-[240px]">
      <div className="flex justify-between items-center mb-5 pb-3 border-b border-slate-50">
        <h4 className="text-[10px] font-black uppercase text-slate-500 tracking-[0.15em]">{title}</h4>
        <span className="text-[9px] font-black px-2.5 py-1 rounded-full bg-slate-100">{weight}</span>
      </div>
      <div className="space-y-4">
        {factors?.map((f, i) => (
          <div key={i} className="flex justify-between items-start">
            <div className="flex flex-col">
              <span className="text-xs font-black text-slate-800">{f.name}</span>
              <span className="text-[9px] font-bold text-slate-400 uppercase">{f.source}</span>
            </div>
            <span className={`text-xs font-black ${f.impact === 'POSITIVE' ? 'text-green-500' : 'text-red-500'}`}>{f.value}%</span>
          </div>
        ))}
      </div>
    </div>
  );
};

